import wmPoster from "@/components/wm-poster/wm-poster.vue"

export default {
    components: {wmPoster},
    data() {
        return {
            providerList: [],
            sourceLink: 'http://yunzhujiao.cn/bind_pub/index.html',
            type: 0,
            url: this.$queue.publicYuMing() + '/?invitation=' + this.$queue.getInvitation(),
            erweima: 'https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=' + this.$queue.publicYuMing(),
        }
    },
    onLoad() {
        let relationId = this.$queue.getData("relation_id");
        if (relationId) {
            this.url = this.$queue.publicYuMing() + '/?invitation=' + relationId;
            this.erweima = 'https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=' + this.$queue.publicYuMing() + '/?invitation=' + relationId;
        }
        uni.getProvider({
            service: 'share',
            success: (e) => {
                let data = [];
                for (let i = 0; i < e.provider.length; i++) {
                    switch (e.provider[i]) {
                        case 'weixin':
                            data.push({
                                name: '分享到微信好友',
                                id: 'weixin'
                            });
                            data.push({
                                name: '分享到微信朋友圈',
                                id: 'weixin',
                                type: 'WXSenceTimeline'
                            });
                            break;
                        case 'qq':
                            data.push({
                                name: '分享到QQ',
                                id: 'qq'
                            });
                            break;
                        default:
                            break;
                    }
                }
                this.providerList = data;
            },
            fail: (e) => {
                console.log('获取登录通道失败' + JSON.stringify(e));
            }
        });
    },
    methods: {
        toSave() {

            uni.getImageInfo({
                src: this.erweima,
                success: function (image) {
                    console.log('图片信息：', JSON.stringify(image));
                    uni.saveImageToPhotosAlbum({
                        filePath: image.path,
                        success: function () {
                            console.log('save success');
                            uni.showToast({
                                title: '图片保存成功',
                                icon: 'none',
                                duration: 3000
                            });
                        }
                    });
                }
            });


        },
        navBack() {
            uni.navigateBack();
        },
        //复制分享链接
        sharurl() {
            uni.setClipboardData({
                data: "给你说个购物省钱的好东西！\n买东西最高能省50%的钱！\n一年省一半\n谁不想给自己省钱呢？\n戳下方链接查看哦啊\n" + this.url,
                success: function () {
                    console.log('success');
                    uni.showModal({
                        title: '复制成功',
                        content: '内容已复制到粘贴板',
                        showCancel: false,
                        success: function (res) {
                            if (res.confirm) {
                                //console.log('用户点击确定');
                            } else if (res.cancel) {
                                //console.log('用户点击取消');
                            }
                        }
                    });
                }
            });
        },
        //保存图片到相册
        save() {
            uni.showActionSheet({
                itemList: ['保存图片到相册'],
                success: () => {
                    plus.gallery.save('http://pds.jyt123.com/wxtest/logo.png', function () {
                        uni.showToast({
                            title: '保存成功',
                            icon: 'none'
                        })
                    }, function () {
                        uni.showToast({
                            title: '保存失败，请重试！',
                            icon: 'none'
                        })
                    });
                }
            })
        },
        share(e) {
            if (this.providerList.length === 0) {
                uni.showModal({
                    title: '当前环境无分享渠道!',
                    showCancel: false
                });
                return;
            }
            let itemList = this.providerList.map(function (value) {
                return value.name
            });


            uni.showActionSheet({
                itemList: itemList,
                success: (res) => {
                    console.log(this.providerList[res.tapIndex].id)
                    if (this.providerList[res.tapIndex].id == 'qq') {
                        this.type = 1
                    } else {
                        this.type = 0
                    }
                    uni.share({
                        provider: this.providerList[res.tapIndex].id,
                        scene: this.providerList[res.tapIndex].type && this.providerList[res.tapIndex].type === 'WXSenceTimeline' ? 'WXSenceTimeline' : "WXSceneSession",
                        type: this.type,
                        title: '耘助教',
                        summary: '耘助教是一个在线教育应用平台',
                        imageUrl: 'http://pds.jyt123.com/wxtest/logo.png',
                        href: "https://m3w.cn/uniapp",
                        success: (res) => {
                            console.log("success:" + JSON.stringify(res));
                        },
                        fail: (e) => {
                            uni.showModal({
                                content: e.errMsg,
                                showCancel: false
                            })
                        }
                    });
                }
            })


        },
        openLink() {
            plus.runtime.openWeb(this.sourceLink)
        }
    }
}


